import React, { Component } from 'react';
import { Grid, Segment } from 'semantic-ui-react';
import Token from './Token';

import BettingBoxToken from './BettingBoxToken';
import RangeSlider from './RangeSlider';

class BettingBox extends Component {
  NUMBER_OF_BOXES = 4;

  constructor(props) {
    super(props);
  }

  renderTokensBet(tokens) {
    const items = [];

    for (let i = 0; i < this.NUMBER_OF_BOXES; i++) {
      const token = tokens[i] || null;
      items.push(
        <Grid.Row key={i}>
          <Grid.Column width={7}>
            <BettingBoxToken token={token} />
          </Grid.Column>
          <Grid.Column width={9} verticalAlign="middle">
            <RangeSlider
              prices={this.props.prices}
              token={token}
              toBet={tokens}
            />
          </Grid.Column>
        </Grid.Row>
      );
    }

    return items;
  }

  // renderTokensBet(tokens) {
  //   const items = [];

  //   for (let i = 0; i < this.NUMBER_OF_BOXES; i++) {
  //     const token = tokens[i] || null;

  //     if (token)
  //       items.push(
  //         <Grid.Row key={i}>
  //           <Grid.Column width={7}>
  //             <Segment
  //               textAlign="center"
  //               className={'betting-box-segment betting-box-segment-filled'}>
  //               <Token token={token} className="token-dropped" />
  //             </Segment>
  //           </Grid.Column>
  //           <Grid.Column width={9} verticalAlign="middle" />
  //         </Grid.Row>
  //       );
  //     else
  //       items.push(
  //         <Grid.Row key={i}>
  //           <Grid.Column width={7}>
  //             <Segment
  //               size={'big'}
  //               textAlign="center"
  //               className={'betting-box-segment'}
  //             />
  //           </Grid.Column>
  //           <Grid.Column width={9} verticalAlign="middle" />
  //         </Grid.Row>
  //       );
  //   }

  //   return items;
  // }

  render() {
    const { tokens, bet } = this.props;

    if (!bet)
      return (
        <div className="betting-box">
          <Grid columns="equal">{this.renderTokensBet(tokens)}</Grid>
        </div>
      );

    return (
      <div className="betting-box">
        <Grid columns="equal">{this.renderTokensBet(bet.tokensBet)}</Grid>
      </div>
    );
  }
}

export default BettingBox;
